"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { FileText, Clock, CheckCircle, AlertCircle, LogOut, User, Settings } from "lucide-react";
import { getAuthJSON, postAuthJSON, AuthAPI } from "@/utils/api";
import { useToast } from "@/components/ui/toast";
import { useRouter } from "next/navigation";
import {
  SidebarProvider,
  SidebarInset,
  SidebarTrigger,
  Sidebar,
  SidebarContent,
  SidebarHeader,
  SidebarMenu,
  SidebarMenuItem,
  SidebarMenuButton,
} from "@/components/ui/sidebar";

export default function SupplierKYCPage() {
  const router = useRouter();
  const { show } = useToast();
  const [kycStatus, setKycStatus] = useState(null);
  const [loading, setLoading] = useState(true);
  const [formData, setFormData] = useState({
    business_license_number: "",
    tax_certificate_number: "",
    documents_path: "",
    notes: "",
  });
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    loadKYCStatus();
  }, []);

  const loadKYCStatus = async () => {
    try {
      setLoading(true);
      const status = await getAuthJSON("/api/v1/supplier-onboarding/kyc/status");
      setKycStatus(status);
      
      // If KYC is approved, redirect to payment step
      if (status.status === 'approved') {
        router.push('/dashboard/supplier-payment');
      }
    } catch (error) {
      console.error("Failed to load KYC status:", error);
      setKycStatus({ status: "not_submitted" });
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    
    try {
      await postAuthJSON("/api/v1/supplier-onboarding/kyc/submit", formData);
      show({ title: "Success", description: "KYC application submitted successfully", variant: "success" });
      loadKYCStatus();
    } catch (error) {
      show({ title: "Error", description: "Failed to submit KYC application", variant: "destructive" });
      console.error(error);
    } finally {
      setIsSubmitting(false);
    }
  };

  const getStatusBadge = () => {
    switch (kycStatus?.status) {
      case "pending":
        return <Badge variant="secondary" className="flex items-center gap-1">
          <Clock className="h-3 w-3" />
          Pending Review
        </Badge>;
      case "approved":
        return <Badge variant="default" className="flex items-center gap-1">
          <CheckCircle className="h-3 w-3" />
          Approved
        </Badge>;
      case "rejected":
        return <Badge variant="destructive" className="flex items-center gap-1">
          <AlertCircle className="h-3 w-3" />
          Rejected
        </Badge>;
      default:
        return <Badge variant="outline">Not Submitted</Badge>;
    }
  };

  const getStatusMessage = () => {
    switch (kycStatus?.status) {
      case "pending":
        return {
          title: "KYC Under Review",
          message: "Your KYC application is being reviewed by our admin team. You will be notified once the review is complete.",
          color: "bg-yellow-50 text-yellow-800"
        };
      case "approved":
        return {
          title: "KYC Approved",
          message: "Your KYC has been approved! You can now proceed to the payment step.",
          color: "bg-green-50 text-green-800"
        };
      case "rejected":
        return {
          title: "KYC Rejected",
          message: "Your KYC application has been rejected. Please review the admin notes and resubmit with correct information.",
          color: "bg-red-50 text-red-800"
        };
      default:
        return null;
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto mb-4"></div>
          <p className="text-muted-foreground">Loading KYC status...</p>
        </div>
      </div>
    );
  }

  if (kycStatus?.status === "approved") {
    return (
      <div className="max-w-2xl mx-auto space-y-6">
        <div className="text-center">
          <CheckCircle className="mx-auto h-16 w-16 text-green-600 mb-4" />
          <h1 className="text-3xl font-bold text-green-600">KYC Approved!</h1>
          <p className="text-muted-foreground mt-2">
            Your supplier account has been verified. You can now proceed to payment.
          </p>
        </div>

        <Card>
          <CardContent className="pt-6">
            <div className="text-center space-y-4">
              <p className="text-sm text-muted-foreground">
                Next step: Submit your payment to activate your supplier account
              </p>
              <Button asChild>
                <a href="/dashboard/supplier-payment">Proceed to Payment</a>
              </Button>
            </div>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="max-w-2xl mx-auto space-y-6">
      <div className="text-center">
        <FileText className="mx-auto h-16 w-16 text-blue-600 mb-4" />
        <h1 className="text-3xl font-bold">
          {kycStatus?.status === 'rejected' ? 'Update KYC Information' : 'KYC Application'}
        </h1>
        <p className="text-muted-foreground mt-2">
          {kycStatus?.status === 'rejected' 
            ? 'Update your KYC information based on admin feedback'
            : 'Complete your KYC verification to become an approved supplier'
          }
        </p>
      </div>

      <Card>
        <CardHeader>
          <div className="flex justify-between items-center">
            <CardTitle>KYC Application Status</CardTitle>
            {getStatusBadge()}
          </div>
        </CardHeader>
        <CardContent>
          {getStatusMessage() && (
            <div className={`p-4 rounded-lg ${getStatusMessage()?.color}`}>
              <h3 className="font-semibold">{getStatusMessage()?.title}</h3>
              <p className="text-sm mt-1">{getStatusMessage()?.message}</p>
              {kycStatus?.admin_notes && (
                <div className="mt-3 p-3 bg-white/50 rounded border">
                  <p className="text-sm">
                    <strong>Admin Notes:</strong> {kycStatus.admin_notes}
                  </p>
                </div>
              )}
            </div>
          )}

          {kycStatus?.status === "not_submitted" && (
            <form onSubmit={handleSubmit} className="space-y-4 mt-4">
              <div className="space-y-2">
                <Label htmlFor="business_license">Business License Number</Label>
                <Input
                  id="business_license"
                  value={formData.business_license_number}
                  onChange={(e) => setFormData(prev => ({
                    ...prev,
                    business_license_number: e.target.value
                  }))}
                  placeholder="Enter your business license number"
                  required
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="tax_certificate">Tax Certificate Number</Label>
                <Input
                  id="tax_certificate"
                  value={formData.tax_certificate_number}
                  onChange={(e) => setFormData(prev => ({
                    ...prev,
                    tax_certificate_number: e.target.value
                  }))}
                  placeholder="Enter your tax certificate number"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="documents">Documents Path</Label>
                <Input
                  id="documents"
                  value={formData.documents_path}
                  onChange={(e) => setFormData(prev => ({
                    ...prev,
                    documents_path: e.target.value
                  }))}
                  placeholder="Path to uploaded documents"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="notes">Additional Notes</Label>
                <Textarea
                  id="notes"
                  value={formData.notes}
                  onChange={(e) => setFormData(prev => ({
                    ...prev,
                    notes: e.target.value
                  }))}
                  placeholder="Any additional information..."
                  rows={3}
                />
              </div>

              <Button type="submit" className="w-full" disabled={isSubmitting}>
                {isSubmitting ? "Submitting..." : "Submit Application"}
              </Button>
            </form>
          )}

          {kycStatus?.status === "rejected" && (
            <form onSubmit={handleSubmit} className="space-y-4 mt-4">
              <div className="space-y-2">
                <Label htmlFor="business_license">Business License Number</Label>
                <Input
                  id="business_license"
                  value={formData.business_license_number}
                  onChange={(e) => setFormData(prev => ({
                    ...prev,
                    business_license_number: e.target.value
                  }))}
                  placeholder="Enter your business license number"
                  required
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="tax_certificate">Tax Certificate Number</Label>
                <Input
                  id="tax_certificate"
                  value={formData.tax_certificate_number}
                  onChange={(e) => setFormData(prev => ({
                    ...prev,
                    tax_certificate_number: e.target.value
                  }))}
                  placeholder="Enter your tax certificate number"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="documents">Documents Path</Label>
                <Input
                  id="documents"
                  value={formData.documents_path}
                  onChange={(e) => setFormData(prev => ({
                    ...prev,
                    documents_path: e.target.value
                  }))}
                  placeholder="Path to uploaded documents"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="notes">Additional Notes</Label>
                <Textarea
                  id="notes"
                  value={formData.notes}
                  onChange={(e) => setFormData(prev => ({
                    ...prev,
                    notes: e.target.value
                  }))}
                  placeholder="Address the admin feedback..."
                  rows={3}
                />
              </div>

              <Button type="submit" className="w-full" disabled={isSubmitting}>
                {isSubmitting ? "Updating..." : "Update Application"}
              </Button>
            </form>
          )}
        </CardContent>
      </Card>
    </div>
  );
}